/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *:before, *:after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

.roboto-regular {
  font-family: 'Roboto', sans-serif; }

.noto-regular {
  font-family: 'Noto Sans', sans-serif;
  font-weight: 400; }

.noto-bold {
  font-family: 'Noto Sans', sans-serif;
  font-weight: 700; }

/* pc */
.wrap {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 30px; }

.header {
  background: #AA0601;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  padding: 0 88px;
  font-size: 20px; }
  .header .logo {
    margin: 30px 0; }
  .header .menu a {
    text-decoration: none;
    color: white; }
  .header h1 a {
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/logo.png");
    width: 80px;
    height: 49px;
    display: block;
    text-indent: 101%;
    overflow: hidden;
    white-space: nowrap;
    float: left; }
  .header ul {
    display: flex;
    list-style-type: none; }
  .header li {
    margin-right: 44px;
    display: block; }
    .header li a {
      padding: 10px; }
  .header li:last-child {
    margin-right: 0px; }

.content .banner {
  background: url("https://hexschool.github.io/webLayoutTraining1st/week3/qa.png") no-repeat center;
  max-width: 1920;
  height: 304px; }
  .content .banner h2 {
    font-size: 64px; }
  .content .banner .wrap {
    height: 100%;
    display: flex;
    align-items: flex-end; }

.content .location-selection {
  display: flex;
  align-items: center;
  font-size: 24px;
  margin-top: 16px; }

.content .selection-box {
  align-self: 20px; }
  .content .selection-box select {
    font-size: 18px;
    padding: 10px 251px 10px 20px;
    border: 1px #CED4DA solid;
    margin-left: 20px; }

.content .info {
  display: flex;
  align-items: center;
  margin-bottom: 16px; }
  .content .info img {
    margin-left: 24px; }

.content .shops {
  margin-top: 32px;
  display: flex;
  flex-wrap: wrap; }

.content .shop {
  width: 30%;
  margin-right: 3%;
  margin-bottom: 32px; }
  .content .shop img {
    display: block; }
  .content .shop h3 {
    font-size: 24px;
    margin-left: 24px; }
  .content .shop .shop-content img {
    width: 21px;
    height: 21px; }
  .content .shop ul {
    margin-top: 23px;
    border: 1px solid #00000020;
    padding: 16px 24px 8px 24px; }
  .content .shop ul li {
    margin-bottom: 8px;
    display: flex;
    align-items: stretch;
    color: #212529;
    align-self: start; }
    .content .shop ul li p {
      font-size: 20px;
      margin-left: 12px;
      line-height: 30px; }
    .content .shop ul li img {
      margin-top: 5px; }
  .content .shop .detail {
    text-align: center;
    background-color: black;
    color: white;
    padding: 20px 0;
    font-size: 19px; }
  .content .shop a {
    display: block; }
  .content .shop button {
    font-size: 19px;
    color: white;
    background: black;
    border: none;
    outline: 0;
    cursor: pointer; }

.content .shop-content {
  margin-top: 12px;
  font-size: 24px; }

.footer {
  background: #AA0601;
  color: white;
  font-size: 16px; }
  .footer .icon {
    width: 48px; }
  .footer a {
    text-decoration: none;
    color: white; }
  .footer a:hover {
    color: #d4d4d4; }
  .footer .footer-bar {
    padding-top: 48px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid white; }
    .footer .footer-bar .footer-bar-menu > ul {
      display: flex; }
    .footer .footer-bar .footer-icon > ul {
      display: flex; }
    .footer .footer-bar li {
      margin-right: 48px; }
      .footer .footer-bar li a {
        text-decoration: none;
        color: white; }
    .footer .footer-bar li:last-child {
      margin-right: 0; }
    .footer .footer-bar a {
      margin-right: 32px; }
    .footer .footer-bar a:last-child {
      margin-right: 0; }
  .footer .footer-icon {
    display: flex; }
  .footer .footer-line-block {
    padding-bottom: 20px; }
    .footer .footer-line-block ul {
      font-size: 36px; }
      .footer .footer-line-block ul li {
        margin-top: 32px; }
      .footer .footer-line-block ul img {
        margin-right: 12px; }
      .footer .footer-line-block ul li {
        display: flex;
        align-items: center; }
  .footer .legal-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 24px;
    padding-bottom: 28px; }
    .footer .legal-block img {
      margin-right: 38px; }
    .footer .legal-block .legal-line {
      display: flex;
      align-items: center;
      flex-grow: 1;
      justify-content: space-between; }
      .footer .legal-block .legal-line .copyright {
        font-size: 16px; }
    .footer .legal-block .legal-words {
      display: flex;
      font-size: 14px; }
      .footer .legal-block .legal-words ul {
        display: flex; }
      .footer .legal-block .legal-words li {
        margin-right: 62px; }
      .footer .legal-block .legal-words li:last-child {
        margin-right: 0; }

/* 768~992px pad */
@media (max-width: 992px) {
  .header {
    padding: 0 30px; }
    .header li {
      margin-right: 20px; }
  .content .shop {
    width: 45%;
    margin-right: 5%; }
    .content .shop h3 {
      font-size: 16px; }
    .content .shop ul li p {
      font-size: 14px;
      line-height: 21px; }
    .content .shop ul li img {
      margin-top: 0px; }
    .content .shop .detail {
      padding: 16px 0;
      font-size: 14px; }
  .footer {
    padding: 0 20px; }
    .footer .footer-bar a {
      margin-right: 24px; }
    .footer .footer-line-block ul li {
      margin-top: 11px;
      font-size: 24px; }
    .footer img {
      width: 24px; }
    .footer .legal-block img {
      display: none; } }

/* 767px handheld below 320 375 414px */
@media (max-width: 767px) {
  .footer .footer-bar li {
    margin-right: 20px; } }

@media (max-width: 576px) {
  .header {
    padding: 0;
    display: flex;
    flex-direction: column; }
    .header ul {
      width: 100vw;
      flex-wrap: wrap;
      justify-content: space-between; }
    .header li {
      width: 50%;
      margin-right: 0;
      text-align: center;
      border: 1px solid white;
      padding: 8px; }
  .wrap-content {
    padding: 0 15px; }
  .content .shop {
    width: 100vw;
    margin-right: 0;
    margin-bottom: 16px; }
  .content .info > img {
    height: 24px; }
  .footer .footer-bar {
    padding-top: 23px; }
    .footer .footer-bar .footer-bar-menu > ul {
      display: none; }
  .footer .footer-line-block ul li {
    font-size: 20px;
    margin-top: 0px;
    line-height: 30px; }
  .footer .legal-block {
    flex-direction: row-reverse; }
    .footer .legal-block a {
      align-self: flex-end; }
    .footer .legal-block .legal-line {
      flex-direction: column;
      align-items: start; }
    .footer .legal-block img {
      display: block;
      margin-right: 0;
      width: 40px; }
    .footer .legal-block .legal-words ul {
      flex-direction: column; }
      .footer .legal-block .legal-words ul li {
        margin-left: 0;
        margin-top: 7px; } }

.faq h2 {
  font-size: 32px;
  margin-top: 80px;
  margin-bottom: 36px; }

.faq h3 {
  font-size: 20px;
  margin-bottom: 16px; }

.faq .line-spacing {
  display: none; }

.faq p {
  margin-left: 94px;
  font-size: 16px;
  line-height: 24px; }

.faq li {
  margin-bottom: 40px; }

@media (max-width: 992px) {
  .faq h2 {
    margin-top: 40px; }
  .faq p {
    margin-left: 60px;
    max-width: 630px; } }

@media (max-width: 576px) {
  .content .banner h2 {
    font-size: 28px; }
  .faq li {
    margin-bottom: 20px; }
  .faq .line-break {
    display: none; }
  .faq .line-spacing {
    display: inline; }
  .faq h2 {
    font-size: 20px;
    margin-bottom: 12px; }
  .faq h3 {
    margin-bottom: 8px;
    font-size: 16px; }
  .faq p {
    margin-left: 0;
    font-size: 14px;
    line-height: 21px; } }
